<!--usertable-->

<template>
	<div id="my">
		<el-row>
			<el-col :span="6" :offset="9">
				<el-input placeholder="" v-model="searchString">
					<el-button slot="append" icon="el-icon-search"></el-button>
				</el-input>
			</el-col>
			<el-col :span="9" style="text-align: right;padding-right:10px;">
				<el-button type="primary" @click="add_user"></el-button>
			</el-col>
		</el-row>

		<myPage
				:tableData="comdata"
				:total="total"
				:currentPage="currentPage"
				:page_sizes="page_sizes"
				:talPro="talPro"
				@handleEditTWO="handleEdit"
				@handleDeleteTWO="handleDelete"
				@handleCk="handleCurrentChange2"
		></myPage>

		<!---->
		<el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
			<el-form ref="form" status-icon :rules="rules2" class="demo-ruleForm" :model="form" label-width="110px" enctype="multipart/form-data">
				<el-form-item style="width:50%;display:inline-block;" v-for="(item,index) in talPro_form" :key="index" :label="item.label + '' "  :prop="item.prop" v-if="item.label === '' ?false:true">
					<el-input v-model="form[item.prop]" v-if="item.select === true ? false:true"  type="text" :disabled="index === 0?true:false"></el-input>

          <!--  -->
          <el-select v-model="form[item.prop]" v-if="item.select === true ? true:false" placeholder="">
            <el-option
              v-for="item2 in item.select_option"
              :key="item2.value"
              :label="item2.label"
              :value="item2.value">
            </el-option>
          </el-select>

				</el-form-item>
          <div style="text-align:right;width:49%;display:inline-block;">
              <el-button type="warning" @click="togoods_detail"></el-button>
          </div>
      </el-form>


			<el-button @click="dialogVisible = false"> </el-button>
			<!--<el-button native-type="submit" type="primary" @click="add_course"> </el-button>-->
			<el-button type="primary" @click="add_course"> </el-button>
		</el-dialog>

    <!-- wangEditor -->
    	<!---->
		<el-dialog title="" :visible.sync="dialogVisible_wang" width="80%" :before-close="handleClose">
        <wangEditor
        @getHtml = "getHtml"
        @getText = "getText"
        @closeDialoge="closeDialoge"
        >

        </wangEditor>
		</el-dialog>
	</div>

</template>

<script>
	//table
  import myPage from "./myPager.vue";
  //import wangEditor from "./wangEditor.vue" ;
  import wangEditor from "../template/tem_wangeditor.vue" ;

	 export default({
    name:'makeupGoods',
	 	data:function(){
	 		var cheack = function(rule,value,callback){
	 					if(!value){
	 						return callback(new Error(" "));
	 					}else{
	 						 callback();
	 					}
	 			};

	 		return {
	 			talPro:[{prop:"_id",label:"id"},{prop:"careType",label:""},{prop:"img",label:""},{prop:"useType",label:""},{prop:"brand",label:""},{prop:"originPrice",label:""},{prop:"newPrice",label:""},{prop:"goodsName",label:""},{prop:"goodsDescribe",label:""},{prop:"goodsDetail",label:""},{prop:"stock",label:""},{prop:"monthlySales",label:""},{prop:"productionPlace",label:""},{prop:"SalesCount",label:""}], //

	 			form:{_id:"",careType:"",img:"",useType:"",brand:"",originPrice:0,newPrice:0,goodsName:"",goodsDescribe:"",goodsDetail:"",stock:0,monthlySales:0,productionPlace:"",SalesCount:""},

         dialogVisible:false,
         dialogVisible_wang:false,

	 			rules2:{
	 				newPrice:[{validator:cheack,trigger:'blur'}]
	 			},

        talPro_form:[//
           {prop:"_id",label:"id"},
           {prop:"careType",label:"",select:true,select_option:[{label:"" , value:""},{label:"" , value:""},{label:"" , value:""}]},
           {prop:"img",label:""},
           {prop:"useType",label:"",select:true,select_option:[{label:"" , value:""},{label:"" , value:""},{label:"" , value:""},{label:"" , value:""},{label:"" , value:""}]},
           {prop:"brand",label:""},
           {prop:"originPrice",label:""},
           {prop:"newPrice",label:""},
           {prop:"goodsName",label:""},
           {prop:"goodsDescribe",label:""},
           {prop:"goodsDetail",label:""},
           {prop:"stock",label:""},
           {prop:"monthlySales",label:""},
           {prop:"productionPlace",label:"",select:true,select_option:[{label:"" , value:""},{label:"" , value:""},{label:"" , value:""},{label:"" , value:""},{label:"" , value:""}]},
           {prop:"SalesCount",label:""}
        ],
	 			searchString:"",
	 			isAdd:true, //
	 			total:12,//
	 			page_sizes:[6],//
	 			currentPage:1,//
	 			//
	 			 tableData: []
	 		}
	 	},
	 	created:function(){ //created
	 		this.getdate(this.currentPage)
	 	},
	 	components:{myPage,wangEditor},
	 	methods:{
	 		handleCurrentChange2(val) {//
		        //console.log(`: ${val}`);
		        this.currentPage = val;
			    	this.getdate(val);
		     },
		     getdate(page){//
		     	var _this = this;
		     	this.$reqs.post('/ModelMakeUp/page',{
                    page:page,
                    rows:_this.page_sizes[0]
                }).then(function(results){
                	_this.total = results.data.count;
                  _this.tableData = results.data.result;
                }).catch(function(er){
                	console.log(er);
                })
		     },
		    handleDelete(index,row){
		     	var _id = row._id;
		     	var _this = this;
		     	this.$confirm('')
					.then(_ => {//
						_this.$reqs.post('/ModelMakeUp/deleteById',{"id":_id}).then(function(result){
              	_this.$message={message:"",type:"success"};
              if(result){

                	_this.getdate(_this.currentPage);
              }

						}).catch(function(e){
							console.log(e);
						})

					})
					.catch(_ => {});
		     },
       handleEdit(index,row){//
	 			this.isAdd = false;
	 			this.form._id = row._id;
        this.form.careType = row.careType;
        this.form.useType = row.useType;
        this.form.brand = row.brand;
        this.form.newPrice = row.newPrice;
        this.form.originPrice =row.originPrice;
        this.form.stock = row.stock;
        this.form.monthlySales = row.monthlySales;
        this.form.productionPlace = row.productionPlace;
        this.form.SalesCount = row.SalesCount;
	 			this.dialogVisible = true;
	 		},
	 		add_user(){//
	 			this.isAdd = true;
	 			this.resetForm("form");
	 			this.dialogVisible = true;
	 		},
	 		handleClose(done) { //
				var _this = this;
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			add_course(){
        var _this = this;
				if(this.isAdd){//
          var da = this.form;
          delete da["_id"];
					this.$reqs.post("/ModelMakeUp/add",{collection:_this.form}).then(function(result){
						if(result.data != null){
              _this.dialogVisible = false;
              _this.getdate(_this.currentPage);
							_this.$message({message:result.data.success,type:"success"});
						}else{
							_this.dialogVisible = false;
							_this.$message({message:result.data.err,type:"error"});
						}
					}).catch(function(er){
						console.log(er);
					});
				}else{//
          var da = this.form;
					this.$reqs.post("/ModelMakeUp/updateById",{selector:da}).then(function(result){

						if(result.data != null){
              _this.$message({ showClose: true,message: '',type: 'success'});
              _this.dialogVisible = false;
               _this.getdate(_this.currentPage);
						}else{
							_this.dialogVisible = false;
							_this.$message({message:result.data.err,type:"error"});
						}
					});
				};

      },
      resetForm(formName){
        if(this.$refs[formName]){//form
          this.$refs[formName].resetFields();
        }

      },
      togoods_detail(){//wangeditor
          this.dialogVisible_wang = true;
      },
      getHtml(html){//wangeditorhtml,
          this.form.goodsDetail = html;
          this.$message({message:"" + html,type:"success"});
          this.dialogVisible_wang = false;
         // console.log(html)
      },
      getText(text){//wangeditorhtml
          console.log(text)
      },
     closeDialoge(){
       this.dialogVisible_wang = false;
     }
	 	},
	 	computed:{
	 		//
	 		//
	 		comdata:function(){
	 			var result = [];
	 			var ss = this.searchString;
        var gg = this.tableData;
	 			gg.forEach(function(item){//
					if((item.careType + "").indexOf(ss) !== -1 || (item._id + "").indexOf(ss) !== -1 ||(item.useType + "").indexOf(ss) !== -1 ||(item.brand + "").indexOf(ss) !== -1 ||(item.originPrice + "").indexOf(ss) !== -1 ||(item.newPrice + "").indexOf(ss) !== -1 ||(item.goodsName + "").indexOf(ss) !== -1 || (item.goodsDescribe + "").indexOf(ss) !== -1 ||(item.goodsDetail + "").indexOf(ss) !== -1 ||(item.stock + "").indexOf(ss) !== -1 || (item.monthlySales + "").indexOf(ss) !== -1|| (item.productionPlace + "").indexOf(ss) !== -1|| (item.SalesCount + "").indexOf(ss) !== -1){
						result.push(item)
					};
				});

				return result;
	 		}
	 	}
	 })



</script>

<style scoped>

</style>



