<template>
  <div id="login">
    <el-row>
      <el-col :span="10" :offset="7">
          <div class="login_inner">
              <el-button-group class="login_btn">
                <el-button type="defult">
                  <router-link to="/"></router-link>
                </el-button>
                <el-button type="info">
                   <router-link to="/register" style="color:white;"></router-link>
                </el-button>
              </el-button-group>

              <el-form style="margin-bottom:20px;" :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="95px" class="demo-ruleForm">
                <el-form-item label="" prop="username">
                  <el-input type="text" v-model="ruleForm2.username" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pass">
                  <el-input placeholder="6" type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pass2">
                  <el-input type="password" placeholder="" v-model="ruleForm2.pass2" auto-complete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="email" label="" auto-complete="off">
                  <el-input type="text" v-model="ruleForm2.email"></el-input>
                </el-form-item> -->
                <el-form-item prop="phone" label="" auto-complete="off">
                  <el-input placeholder="" type="text" v-model="ruleForm2.phone"></el-input>
                </el-form-item>

                <div class="login_more">
                  <el-checkbox v-model="checked" class="remerber">******</el-checkbox>
                  <p></p>
                </div>
                <el-form-item class="login_submit">
                  <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
                  <el-button @click="resetForm('ruleForm2')"></el-button>
                </el-form-item>
              </el-form>
          </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name:"register",
  data() {
    var zz = /^\d{6}$/;
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }else if(zz.test(value)){
          callback();
        } else {
          callback(new Error('6'));
        }
      };
      var checkName= (rule,value,callback) =>{
          if(value === ""){
            callback(new Error(''));
          }else{
            callback();
          }
      };
      var pass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        checked:false,
        ruleForm2: {
          // email:'',
          pass: '',
          pass2: '',
          username:"",
          phone:""
        },
        rules2: {
          username:[
            { validator: checkName, trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          pass2:[
            { validator: pass2, trigger: 'blur' }
          ],
          // email:[
          //   {required:true,message:'',trigger:'blur'},
          //   { validator: checkName, trigger: 'blur' }
          // ],
          phone:[
            {required:true,message:'',trigger:'blur'} //
          ]
        }
      };
    },
    methods: {
      submitForm(formName) { //
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {//
                if(_this.checked){ //
                     _this.$reqs.post('/users/register',{user:_this.ruleForm2}).then(result =>{
                        if(result.data.success){
                              _this.$message({message:"", type:'success'});
                              _this.$router.push('/');
                        }else{
                            _this.$message({message:result.data.err, type:'error'});
                        }
                      }).catch(err =>{
                            console.log(err);
                      })
                }else{ //
                      _this.$message({'message':"",type:'error'})
                }

          } else {//
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
}
</script>

<style scoped>
     #login{border-bottom:5px solid rgb(241,241,241);padding:80px 0 40px;background:url('../assets/images/login_03.png')left top no-repeat;background-size:cover;}
    .login_inner{background:white;padding:20px calc(10% + 50px) 40px;border:1px solid rgb(200,200,200);}
    .login_inner .login_btn{margin-bottom:25px;}
    .login_more{text-align: left;}
    .login_more .remerber{margin-left:80px;}
    .login_more p{font-size:14px;text-align: left;padding-left:80px;}
</style>





