<template>
    <div id="onefoot" style="font-size:12px;background:rgb(247,247,247);">
        <el-row>
          <el-col :span="14" :offset="5">
              <p class="p1">
                <span v-for='(i,index) in top1' :key='index'>
                    <a class="bor_r" :href="i.url">
                      {{i.label}}
                    </a>
                </span>
              </p>
              <p class="p2">
                <span class="bor_r" v-for='(i,index) in top2' :key='index'>
                      {{i}}
                </span>
              </p>
              <p>
                <span v-for='(i,index) in top3' :key='index'>
                      {{i}}
                </span>
              </p>
          </el-col>
        </el-row>

    </div>
</template>


<script>
export default {
    name:"onefoot",
    data(){
      return {
          top1:[{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""},{label:"",url:""}]
          ,top2:["","ICP","------"]
          ,top3:["","",""]
          ,top4:["Copyright"]
      }
    }
}
</script>


<style scoped>
.bor_r{border-right:1px solid rgb(113,113,113);padding:0 10px;}
#onefoot{position:absolute;bottom:0;left:0;width:100%;}

</style>




