var slis = function(th) {



    var handler = function(captchaObj) {
        captchaObj.appendTo('#captcha');
        captchaObj.onReady(function() {
            $("#wait").hide();
        });
        $('#btn').click(function() {
                var result = captchaObj.getValidate();
                if (!result) {
                    return alert('');
                }
                $.ajax({
                    url: 'http://localhost:3000/users/login', //
                    type: 'POST',
                    dataType: 'json',
                    xhrFields: { //session
                        withCredentials: true
                    },
                    data: {
                        username: th.ruleForm2.username,
                        password: th.ruleForm2.pass,
                        geetest_challenge: result.geetest_challenge,
                        geetest_validate: result.geetest_validate,
                        geetest_seccode: result.geetest_seccode
                    },
                    success: function(data) {
                        if (data.status === 'success') {
                            //alert('');
                            console.log(th);
                            th.$router.push({ path: '/index_one' });
                        } else if (data.status === 'error') {
                            console.log(th.ruleForm2.pass)
                            alert(data.info);
                            captchaObj.reset();
                        } else if (data.status === 'fail') {
                            alert('');
                            captchaObj.reset();

                        }
                    }
                });
            })
            // http://docs.geetest.com/install/client/web-front/
    };


    $.ajax({
        url: "http://localhost:3000/users/gt/register-slide?t=" + (new Date()).getTime(), // 
        type: "get",
        dataType: "json",
        success: function(data) {

            //  initGeetest 
            // 1
            // 2
            initGeetest({
                //  4 
                gt: data.gt,
                challenge: data.challenge,
                offline: !data.success, // 
                new_captcha: data.new_captcha, // 

                product: "float", // floatpopup
                width: "220px"
                    // http://docs.geetest.com/install/client/web-front/
            }, handler);
        }
    });

};
export default slis;


